@use "global" as *;

.sidebar-card {
    height: 100%;
    box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);
    background-color: #fff;
    display: flex;
    flex-direction: column;
}

.sidebar-card + .sidebar-card {
    margin-top: rem(40);
}

.sidebar-card__img img{
    object-fit: cover;
    aspect-ratio: 294/188;
    width: 100%;
}

.sidebar-card__content {
    padding: rem(16) rem(23) rem(24) rem(23);
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    text-align: center;
}

.sidebar-card__title {
    font-size: rem(20);
    font-weight: $medium;
    line-height: 1.5;
    flex-grow: 1;
    @include mq(md) {
        margin-top: rem(5);
    }
}

.sidebar-card__lead {
    text-align: center;
    font-size: rem(16);
    border-top: 1px solid $accent-color;
    padding-top: rem(8);
    margin-top: rem(6);
    line-height: 1.71429;
}

.sidebar-card__layout {
    display: flex;
    align-items: flex-end;
    margin-top: rem(8);
    gap: rem(16);
    justify-content: center;
}

.sidebar-card__before {
    font-family: $second-font-family;
    font-size: rem(20);
    font-weight: $bold;
    line-height: 1;
    margin-top: rem(5);

    white-space: nowrap;
    @include mq(md) {
        margin-top: rem(12);
        min-width: 0;
    }
}

